.spinner {
   backdrop-filter: blur(3px);
   background-color: rgba(255, 255, 255, 0.3);
   position: absolute;
   width: 100%;
   height: 100%;
   z-index: 200;
   svg {
      position: relative;
      height: 70px;
      width: 70px;
      top: 50%;
      transform: translateY(-50%);
   }
 }